<template>
  <div id="storageForm">
    <navBar :title='title'
            :color="colorIf" />

    <van-form class="storageForm-form"
              validate-first
              @submit="onFailed">

      <!--  -->
      <van-cell-group>
        <van-field v-model="formData.administrator"
                   name="administrator"
                   label=""
                   input-align="right"
                   required
                   placeholder="" />
      </van-cell-group>

      <!--  -->

      <van-field readonly
                 clickable
                 label=""
                 name="name"
                 input-align="right"
                 :value="formData.name[formData.name.length-1]"
                 placeholder=""
                 required
                 @click="showPicker = true" />
      <van-popup v-model="showPicker"
                 round
                 position="bottom">
        <van-picker show-toolbar
                    :columns="columns"
                    @cancel="showPicker = false"
                    @confirm="onConfirmName" />

      </van-popup>

      <!--  -->

      <van-field readonly
                 clickable
                 label=""
                 input-align="right"
                 name="unit"
                 required
                 :value="formData.unit"
                 placeholder=""
                 @click="showPickerUnit = true" />
      <van-popup v-model="showPickerUnit"
                 round
                 position="bottom">
        <van-picker show-toolbar
                    :columns="columnsUnit"
                    @cancel="showPickerUnit = false"
                    @confirm="onConfirmUnit" />
      </van-popup>

      <!--  -->

      <van-field v-model="formData.quantity"
                 name="quantity"
                 placeholder=""
                 input-align="right"
                 type="digit"
                 required
                 label="" />

      <!--  -->
      <van-field readonly
                 clickable
                 name="calendar"
                 :value="formData.date"
                 label=""
                 input-align="right"
                 required
                 placeholder=""
                 @click="showCalendar = true" />
      <van-calendar v-model="showCalendar"
                    @confirm="onConfirm"
                    color="#fe8407" />

      <!--  -->
      <van-cell-group>
        <van-field v-model="formData.administrator"
                   name="administrator"
                   label=""
                   input-align="right"
                   required
                   placeholder="" />
      </van-cell-group>

      <div style="margin: 16px;">
        <van-button round
                    block
                    type="info"
                    color="#fe8407"
                    native-type="submit">
          
        </van-button>
      </div>
    </van-form>

  </div>
</template>
<script>

import navBar from '@/components/navBar.vue'
import BScroll from 'better-scroll'
export default {
  components: {
    navBar
  },
  data () {
    return {
      title: '',
      colorIf: true,
      formData: {
        date: '',
        name: [],
        administrator: '',
        quantity: '',
        unit: ''
      },
      showCalendar: false,
      showPicker: false,
      showPickerUnit: false,
      columnsUnit: ['', ''],
      columns: [
        {
          text: '',
          children: [
            {
              text: '-1.5'
            },
            {
              text: '-2'
            },
            {
              text: '-4'
            },
            {
              text: '-10'
            },
          ],
        },
        {
          text: '',
          children: [
            {
              text: '-1.8'
            }
          ],
        },
      ],

    }
  }, mounted () {

  },
  methods: {
    onFailed (errorInfo) {
      this.$notify({ type: 'success', message: '' });
      this.$router.push({
        path: '/comeLibrary'
      })
    },
    onConfirm (date) {
      this.formData.date = `${date.getMonth() + 1}/${date.getDate()}`;
      this.showCalendar = false;
    },
    onConfirmName (value) {
      console.log(value)
      this.formData.name = value;
      this.showPicker = false;
    },
    onConfirmUnit (value) {
      this.formData.unit = value;
      this.showPickerUnit = false;
    }
  }
}
</script>
<style lang="stylus">
.wrapper
  height 100vh
  overflow hidden
#storageForm
  position relative
  overflow hidden
  &:after
    position absolute
    content ''
    width 600px
    height 600px
    background linear-gradient(to top left, #fe8407 20%, #f5a13f 100%)
    border-radius 50%
    transform translate(-38%, -72%)
    top 0
    z-index -1
    animation mymove 10s infinite
  &:before
    position absolute
    content ''
    width 300px
    height 300px
    border-radius 50%
    background-color #fcd2a8
    transform translate(80%, -50%)
    top 0
    z-index -1
    animation mymove2 10s infinite
  .storageForm-form
    background-color #ffffff
    margin 0 15px
    border-radius 12px
    overflow hidden
    box-shadow 0 0 5px #b6b0aa
@keyframes mymove
  0%
    transform translate(-38%, -72%)
  50%
    transform translate(-45%, -70%)
  100%
    transform translate(-38%, -72%)
@keyframes mymove2
  0%
    transform translate(80%, -50%)
  50%
    transform translate(85%, -40%)
  100%
    transform translate(80%, -50%)
</style>