<template>
  <div id="inventory">
    <navBar :title='title'
            :color="colorIf" />
    <div id="main"
         style="width: 100%;height:200px;margin-bottom:15px"></div>

    <ul class="inventory-ul">
      <li class="inventory-li"
          v-for="(item,index) in inventory"
          :key="index">
        <div class="inventory-left">{{item.name}}</div>
        <div class="inventory-right">
          <span>{{item.quantity}}</span>
          <i>{{item.unit}}</i>
        </div>
      </li>
    </ul>

  </div>
</template>


<script>


import navBar from '@/components/navBar.vue'

export default {
  components: {
    navBar
  },
  data () {
    return {
      title: '',
      colorIf: false,
      inventory: [
        {
          name: '-1.5',
          unit: '',
          quantity: '100'
        },
        {
          name: '-2',
          unit: '',
          quantity: '40'
        },
        {
          name: '-4',
          unit: '',
          quantity: '23'
        },
        {
          name: '-10',
          unit: '',
          quantity: '20'
        },
        {
          name: '-1.8',
          unit: '',
          quantity: '60'
        }
      ]

    }
  },
  mounted () {
    this.drawChart()
  },
  methods: {
    drawChart () {
      // domecharts
      let myChart = this.$echarts.init(document.getElementById("main"));

      // 
      let option = {
        backgroundColor: "#fe8407",
        title: {
          text: '',
          textStyle: {
            align: 'center',
            color: '#fff',
            fontSize: 16,
          },
          top: '5%',
          left: 'center',
        },
        tooltip: {
        },
        grid: {
          top: '10%',
          left: '10%',
          right: '10%',
          bottom: '5%',
          containLabel: true,
        },
        xAxis: [{
          type: 'category',
          boundaryGap: false,
          axisLine: { //x
            show: false,
            lineStyle: {
              color: '#ffffff'
            },
          },
          axisLabel: { //
            textStyle: {
              color: '#ffffff',
              margin: 10,
            },
          },
          axisTick: { show: false, },
          data: ['6.11', '6.12', '6.13', '6.14', '6.15'],
        }],
        yAxis: [{
          type: 'value',
          splitNumber: 3,
          splitLine: {
            show: false,
            lineStyle: {
              color: '#ffffff'
            }
          },
          axisLine: { show: false, },
          axisLabel: {
            show: false,
            margin: 20,
            textStyle: {
              color: '#ffffff',

            },
          },
          axisTick: { show: false, },
        }],
        series: [{
          name: '',
          type: 'line',
          smooth: true, //
          // 			symbol:'circle',  // 
          symbolSize: 0,

          lineStyle: {
            normal: {
              color: "#ffffff"   // 
            }
          },

          data: [90, 105, 84, 125, 110]
        }]
      };


      // 
      myChart.setOption(option);
    }

  }
}
</script>

<style lang="stylus">
#inventory
  .inventory-ul
    .inventory-li
      display flex
      height 60px
      margin 0 15px 15px
      justify-content space-between
      align-items center
      padding 0 15px
      border-radius 4px
      box-shadow 0 0 15px #c5c5c5
      &:nth-child(1)
        background-color #f44336
      &:nth-child(2)
        background-color #ff9800
      &:nth-child(3)
        background-color #ffeb3b
      &:nth-child(4)
        background-color #8bc34a
      &:nth-child(5)
        background-color #9c27b0
      .inventory-left
        font-size 16px
        color #ffffff
      .inventory-right
        align-items baseline
        display flex
        span
          display inline-block
          margin-right 5px
          font-size 16px
          color #ffffff
        i
          font-size 12px
          color #ffffff
          font-style normal
</style>

